import { useState, useEffect } from 'react';
import Top from './Top';
import Center from './Center';
import Footer from './Footer';
import PersonalOption from './PersonalOption';

const OptionSection = ({ onClose }: { onClose: () => void }) => {

    const [showPersonalOption, setShowPersonalOption] = useState(false);
    const [showExtraOption, setShowExtraOption] = useState(false);

    const stopPropagation = (e: React.MouseEvent) => {
        e.stopPropagation();
    };

    function personalOptionClose() {
        setShowPersonalOption(false);
    }
    useEffect(() => {

        console.log(showPersonalOption);
        console.log(showExtraOption);
    }, [showPersonalOption, showExtraOption]);
    return (
        <section className='OptionSection' onClick={onClose}>
            <div className='optionBox' onClick={stopPropagation}>
                {showPersonalOption && (
                    <PersonalOption personalOptionClose={personalOptionClose} />
                )}
                <Top
                    onClose={onClose}></Top>
                <Center
                    setShowExtraOption={setShowExtraOption}
                    setShowPersonalOption={setShowPersonalOption}></Center>
                <Footer></Footer>
            </div>
        </section>
    );
};

export default OptionSection;