import { atom } from 'recoil';

// 지점별 코드
export const branchCodeState = atom<string | null>({
    key: 'branchCodeState',
    default: ""
})
export const kioskNumberState = atom<string | null>({
    key: 'kioskNumberState',
    default: ""
})

interface MenuOptionDetails {
    option_item_name: string;
    option_item_price: number;
    option_item_order: number;
    details: any[];  // DetailType은 세부 항목의 구체적인 타입
}

// 각 카테고리는 이제 객체로 정의되며, 각 키는 MenuOptionDetails 객체를 값으로 가집니다.
interface OptionCategories {
    [key: string]: MenuOptionDetails[];
}

interface MenuInfoType {
    major_category_name: string;
    menu_category_name: string;
    menu_name: string;
    menu_code: string;
    menu_id: number;
    menu_price: number;
    menu_imagepath: string;
    menu_order: number;
    menu_default_option_code: string;
    options: {
        basic: OptionCategories;
        personal: OptionCategories;
        extra: OptionCategories;
    };
}


export const menuInfoState = atom<MenuInfoType | null>({
    key: 'menuInfoState',
    default: null,
})

export const addMenuBasketState = atom<any>({
    key: 'addMenuBasketState',
    default: {
        menuName: '',
        menuCode: '',
        menuOptionCode : '',
        options : {
            basic: {},
            personal:{},
            extra:{}
        },
        basePrice: 0,
        menuPrice: 0,
        menuCount: 0,
        totalPrice : 0,
    },
})

export const basicOptionPriceState = atom<number>({
    key: 'basicOptionPriceState',
    default: 0,
})
export const personalOptionPriceState = atom<number>({
    key: 'personalOptionPriceState',
    default: 0,
})
export const extraOptionPriceState = atom<number>({
    key: 'extraOptionPriceState',
    default: 0,
})