import React, { useEffect } from 'react';

function CallPage() {
  useEffect(() => {
    handleCall();
  }, []);

  const handleCall = () => {
    window.location.href = 'tel:00000000000';
  }

  return (
    <div>ㄴ
      <button onClick={handleCall}>전화 걸기</button>
    </div>
  );
}

export default CallPage;
