import { useEffect, useState } from 'react';
import axios from 'axios';
import { useRecoilValue } from 'recoil';
import { branchCodeState } from '../../atoms/recoil';
interface ChoiceCategoryProps {
    choiceCategory: string;
    setChoiceCategory: (category: string) => void;
}
interface MenuCategory {
    id: number;
    menu_category_name: string;
    menu_category_order: number;
    major_category_name: string;
}
const Category = ({ choiceCategory, setChoiceCategory }: ChoiceCategoryProps) => {
    const branchCode = useRecoilValue(branchCodeState);
    const [menuCategoryList, setMenuCategoryList] = useState<MenuCategory[]>([]);

    async function getCategoryByBranch(branchCode: string | null) {
        console.log(branchCode);
        try {
            // https://nanonix.lol/Branch/getCategoryByBranch?Branch=JR01
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/getCategoryByBranch?branchCode=${branchCode}`);
            if (response.status === 200) {        
                // console.log(response.data);
                
                // 배열 길이 10으로 맞추기
                let categories = response.data;
                while (categories.length < 10) {
                    categories.push({ id: null, menu_category_name: '', menu_category_order: null, major_category_name: '' });
                }
                setMenuCategoryList(categories);
                setChoiceCategory(categories[0].menu_category_name);
            }
        } catch (error) {
            console.error(error);
        }
    }
    useEffect(() => {
        if (branchCode === "") return;
        getCategoryByBranch(branchCode);
    }, [branchCode]);

    
    function categoryTouch(choiceCategory: string) {
        if (choiceCategory === "") return;
        setChoiceCategory(choiceCategory)
    }


    useEffect(() => {
        // 카테고리 빈값 확인
        // http://localhost:3000?branchCode=JR01&kioskNumber=1
        // console.log("카테고리");
        // console.log(menuCategoryList);
        if(menuCategoryList[0]?.id === null && branchCode !== undefined){
            getCategoryByBranch(branchCode);
            return;
        }
    }, [menuCategoryList]);


    return (
        <section className='Category'>
            <article className='menuCategory'>
                {menuCategoryList.map((category: any, index: number) => {
                    return (
                        <div className={`categoryBox ${choiceCategory == category.menu_category_name ? "selectedCategory" : ''}`}
                            key={`${index}_${category.menu_category_name}`} onClick={() => categoryTouch(category.menu_category_name)}>
                            {category.menu_category_name}
                        </div>
                    );
                })}
            </article>
            <article className='topLine'>

            </article>
        </section>

    );
};

export default Category;