import { useState, useEffect } from "react";
import axios from "axios";


const SettingBranchSelect = ({ selectBranch, setSelectBranch }: any) => {
    const [searchBranchName, setSearchBranchName] = useState<string>("");
    const [branchList, setBranchList] = useState<any>([]);

    async function getBranchList(searchBranchName: string) {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getBranchList`, {
                searchBranchName: searchBranchName
            });
            if (response.status === 200) {
                setBranchList(response.data);
            }
        } catch (error: any) {
            console.log(error);
        }
    }

    async function deleteBranch(deleteBranchName: string) {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/deleteBranch`, {
                deleteBranchName: deleteBranchName
            });
            if (response.status === 200) {
                window.location.reload();
            }
        } catch (error: any) {
            console.log(error);
        }
    }

    useEffect(() => {
        getBranchList(searchBranchName);
    }, [searchBranchName]);

    return (
        <div className="SettingBranchSelect">
            <h1>지점 선택</h1>
            <p> 지점 찾기 :&nbsp;
                <input
                    type="text"
                    placeholder="지점명을 입력해주세요."
                    value={searchBranchName}
                    onChange={(e: any) => setSearchBranchName(e.target.value)}
                />
            </p>
            <div>
                {branchList.map((item: any, index: number) => {
                    return (
                        <div key={item.branch_name} className="seletedBranch" >
                            <label htmlFor={`checkbox-${item.branch_name}`}>
                                <input
                                    // style={{ display: "none" }}
                                    id={`checkbox-${item.branch_name}`} // input에 고유 ID 추가
                                    name={item.branch_name}
                                    type="checkbox"
                                    checked={selectBranch === item.branch_name}
                                    onChange={() => setSelectBranch(item.branch_name)}
                                />
                                
                                    <h2>{item.branch_name}</h2> {/* 이제 이 h2를 클릭해도 체크박스가 토글됩니다 */}
                                    <button onClick={() => {deleteBranch(item.branch_name)}}>삭제</button>
                                
                            </label>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default SettingBranchSelect;