import { useState, useEffect } from 'react';
import axios from 'axios';
import { useRecoilState, useRecoilValue } from 'recoil';
import { menuInfoState, addMenuBasketState, branchCodeState } from '../../atoms/recoil';
import OptionSection from './Option/OptionSection';

interface ChoiceCategoryProps {
    choiceCategory: string;
}

interface MenuListType {
    branch_menu_active: boolean;
    id: number;
    menu_order: number;
    menu_name: string;
    menu_imagepath: string
    menu_default_option_code: string;
}

interface OptionItem {
    option_item_name: string;
    // 다른 속성들...
}

const MenuList = ({ choiceCategory }: ChoiceCategoryProps) => {
    const [menuList, setMenuList] = useState<MenuListType[]>([]);
    const [menuInfo, setMenuInfo] = useRecoilState(menuInfoState);
    const branchCode = useRecoilValue(branchCodeState);
    const [addMenuBasket, setAddMenuBasket] = useRecoilState(addMenuBasketState);

    // 카테고리 선택시 메뉴 받기
    async function getMenuByCategory(categoryName: string) {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Branch/getMenuByCategory?branchCode=${branchCode}&categoryName=${categoryName}`);
            if (response.status === 200) {
                setMenuList(response.data);
                // console.log(response.data);

            }
        } catch (error) {
            console.error(error);
        }
    }
    // 선택한 메뉴 정보 받기
    async function getMenuInfo(menuName: string) {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Branch/getMenuInfo?branchCode=${branchCode}&menuName=${menuName}`);
            if (response.status === 200) {
                // console.log(response.data)
                setMenuInfo(response.data);

                setAddMenuBasket((prevState: any) => ({
                    ...prevState,
                    menuName: response.data.menu_name,
                    menuCode: response.data.menu_code,
                    menuOptionCode: response.data.menu_default_option_code,
                    options: {
                        basic: {},
                        personal: {},
                        extra: {}
                    },
                    basePrice: response.data.menu_price,
                    menuPrice: 0,
                    menuCount: 1,
                    totalPrice: 0,

                }));

            }
        } catch (error) {
            console.error(error);
        }
    };

    const closeOption = () => {
        setMenuInfo(null);
        setAddMenuBasket(null);
    };

    // 카테고리 선택시 메뉴받기
    useEffect(() => {
        if (choiceCategory === "") return;
        getMenuByCategory(choiceCategory);
    }, [choiceCategory]);


    return (
        <div className='MenuList'>
            {menuInfo && (
                <OptionSection
                    onClose={closeOption}
                />
            )}
            {menuList.map((item: any, index: number) => {
                return (
                    <div
                        className={`menuListBox ${item.branch_menu_active === false ? "soldout" : null}`}
                        key={item.menu_name}
                        onClick={() => { getMenuInfo(item.menu_name) }}
                    >

                        <div className='menuImage'>
                            <img src={`https://${process.env.REACT_APP_SERVER_IP}/${item.menu_imagepath}`} alt="메뉴이미지" />
                        </div>
                        <div className='menuInfo'>
                            <p className='menuName'>{item.menu_name}</p>
                            <p className='menuPrice'>{item.menu_price.toLocaleString('ko-KR')}원</p>
                        </div>
                    </div>
                )
            })}
        </div>
    );
};

export default MenuList;