import axios from 'axios';
import { useState, useEffect } from 'react';

const SettingProductCreateOption = ({ setOptionState }: any) => {

    const [option, setOption] = useState<any>([]);
    const [showOption, setShowOption] = useState<boolean>(false);
    const [basicOption, setBasicOption] = useState<any>([]);
    const [personalOption, setPersonalOption] = useState<any>([]);
    const [extraOption, setExtraOption] = useState<any>([]);

    async function getDetailOption(funtionName: string) {

        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/${funtionName}`);
            if (response.status === 200) {
                console.log(response.data);
                for (const option of response.data) {
                    console.log("===============================================");
                    option.options[0].option_item_isChecked = true;
                    option.options[0].option_item_isDefault = true;
                    console.log("===============================================");
                }
                if (funtionName === "getBasicOption") {
                    setBasicOption(response.data);
                } else if (funtionName === "getPersonalOption") {
                    setPersonalOption(response.data);
                } else {
                    setExtraOption(response.data);
                }
            }
        } catch (error) {
            console.error('Error:', error);
        }
    }

    useEffect(() => {
        getDetailOption("getBasicOption");
        getDetailOption("getPersonalOption");
        getDetailOption("getExtraOption");
    }, []);

    useEffect(() => {
        console.log("====================================================================");
        console.log("basicOption");
        console.log(basicOption);

    }, [basicOption]);

    useEffect(() => {
        console.log("====================================================================");
        console.log("personalOption");
        console.log(personalOption);

    }, [personalOption]);

    useEffect(() => {
        console.log("====================================================================");
        console.log("extraOption");
        console.log(extraOption);
    }, [extraOption]);

    // useEffect(() => {
    //     console.log("====================================================================");
    //     // console.log(basicOption);

    // }, [basicOption]);

    //옵션 사용여부 체크박스 변경
    function handleBasicOptionUseChange(optionIndex: number, optionItemIndex: number) {
        setBasicOption((prev: any) => {
            return prev.map((option: any, index: any) => {
                if (index === optionIndex) {
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: any) => {
                            if (idx === optionItemIndex) {
                                return {
                                    ...optionItem,
                                    option_item_isChecked: !optionItem.option_item_isChecked, // 값 반전
                                };
                            }
                            return optionItem;
                        }),
                    };
                }
                return option;
            });
        });
    }
    function handlePersonalOptionUseChange(optionIndex: number, optionItemIndex: number) {
        setPersonalOption((prev: any) => {
            return prev.map((option: any, index: any) => {
                if (index === optionIndex) {
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: any) => {
                            if (idx === optionItemIndex) {
                                return {
                                    ...optionItem,
                                    option_item_isChecked: !optionItem.option_item_isChecked, // 값 반전
                                };
                            }
                            return optionItem;
                        }),
                    };
                }
                return option;
            });
        });
    }
    function handleExtraOptionUseChange(optionIndex: number, optionItemIndex: number) {
        setExtraOption((prev: any) => {
            return prev.map((option: any, index: any) => {
                if (index === optionIndex) {
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: any) => {
                            if (idx === optionItemIndex) {
                                return {
                                    ...optionItem,
                                    option_item_isChecked: !optionItem.option_item_isChecked, // 값 반전
                                };
                            }
                            return optionItem;
                        }),
                    };
                }
                return option;
            });
        });
    }

    // 기본 사용여부 체크
    function handleBasicOptionDefaultChange(optionIndex: number, optionItemIndex: number) {
        setBasicOption((prev: any) => {
            return prev.map((option: any, index: number) => {
                if (index === optionIndex) { // 선택한 옵션 찾기
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: number) => {
                            if (idx === optionItemIndex) {
                                // 선택된 항목을 기본값으로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: true,
                                };
                            } else {
                                // 다른 모든 항목의 기본값을 false로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: false,
                                };
                            }
                        }),
                    };
                }
                return option;
            });
        });
    }
    function handlePersonalOptionDefaultChange(optionIndex: number, optionItemIndex: number) {
        setPersonalOption((prev: any) => {
            return prev.map((option: any, index: number) => {
                if (index === optionIndex) { // 선택한 옵션 찾기
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: number) => {
                            if (idx === optionItemIndex) {
                                // 선택된 항목을 기본값으로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: true,
                                };
                            } else {
                                // 다른 모든 항목의 기본값을 false로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: false,
                                };
                            }
                        }),
                    };
                }
                return option;
            });
        });
    }
    function handleExtraOptionDefaultChange(optionIndex: number, optionItemIndex: number) {
        setExtraOption((prev: any) => {
            return prev.map((option: any, index: number) => {
                if (index === optionIndex) { // 선택한 옵션 찾기
                    return {
                        ...option,
                        options: option.options.map((optionItem: any, idx: number) => {
                            if (idx === optionItemIndex) {
                                // 선택된 항목을 기본값으로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: true,
                                };
                            } else {
                                // 다른 모든 항목의 기본값을 false로 설정
                                return {
                                    ...optionItem,
                                    option_item_isDefault: false,
                                };
                            }
                        }),
                    };
                }
                return option;
            });
        });
    }

    // 옵션 사용여부가 true인 항목만 필터링하고 상태에 저장하는 함수
    function filterSetOptionState() {
        const filteredOptions:any = {};
    
        // 기본 옵션 필터링 및 저장
        const filteredBasicOption = basicOption.map((option:any) => ({
            ...option,
            options: option.options.filter((item:any) => item.option_item_isChecked),
        })).filter((option:any) => option.options.length > 0);  // 옵션 항목이 비어있지 않은 경우만 저장
        filteredOptions['basic'] = filteredBasicOption;
    
        // 퍼스널 옵션 필터링 및 저장
        const filteredPersonalOption = personalOption.map((option:any) => ({
            ...option,
            options: option.options.filter((item:any) => item.option_item_isChecked),
        })).filter((option:any) => option.options.length > 0);
        filteredOptions['personal'] = filteredPersonalOption;
    
        // 엑스트라 옵션 필터링 및 저장
        const filteredExtraOption = extraOption.map((option:any) => ({
            ...option,
            options: option.options.filter((item:any) => item.option_item_isChecked),
        })).filter((option:any) => option.options.length > 0);
        filteredOptions['extra'] = filteredExtraOption;
    
        // 결과적으로 필터링된 옵션들을 상위 컴포넌트로 전달
        setOptionState(filteredOptions);
    }
    
    useEffect(() => {
        filterSetOptionState();
    }, [basicOption, personalOption, extraOption]);

    return (

        <div className='SettingProductCreateOption'>
            {/* 옵션 선택 */}
            <section className='productSettingLine'>
                <div className='productSettingName'>옵션 선택</div>
                <div className='productSettingValue'>
                    <button onClick={() => setShowOption(!showOption)}>{showOption ? '옵션 숨기기' : '옵션 보기'}</button>
                </div>
            </section>
            <section>
                {/* 기본옵션 */}
                <div>
                    <h1>
                        기본 옵션
                    </h1>
                </div>
                {showOption && basicOption.map((option: any, index: any) => (
                    <article key={option.option_name} className='optionSection'>
                        <div className='optionHeader'>
                            <h2>{option.option_name}</h2>
                            <div className='optionHeaders'>
                                <span>옵션번호</span>
                                <span>옵션명</span>
                                <span>옵션 사용여부</span>
                                <span>기본값 여부</span>
                            </div>
                        </div>
                            {option.options.map((optionItem: any, idx: any) => (
                                <div key={`${option.option_name}_${idx}`} className='optionItem'>
                                    <span>{optionItem.option_item_order + 1}</span>
                                    <span>{optionItem.option_item_name}</span>
                                    <span>
                                        <input
                                            type="checkbox"
                                            className='checkBox'
                                            checked={optionItem.option_item_isChecked}
                                            onChange={() => { handleBasicOptionUseChange(index, idx) }}

                                        />

                                    </span>
                                    <span>
                                        <input
                                            type="radio"
                                            className='radioButton'
                                            name={option.option_name}
                                            checked={optionItem.option_item_isDefault}
                                            onChange={() => { handleBasicOptionDefaultChange(index, idx) }}

                                        />
                                    </span>

                                </div>
                            ))}
                    </article>
                ))}

                {/* 퍼스널 옵션 */}
                <div>
                    <h1>
                        퍼스널 옵션
                    </h1>
                </div>
                {showOption && personalOption.map((option: any, index: any) => (
                    <article key={option.option_name} className='optionSection'>
                        <div className='optionHeader'>
                            <h2>{option.option_name}</h2>
                            <div className='optionHeaders'>
                                <span>옵션번호</span>
                                <span>옵션명</span>
                                <span>옵션 사용여부</span>
                                <span>기본값 여부</span>
                            </div>
                        </div>
                        <div className='optionItems'>
                            {option.options.map((optionItem: any, idx: any) => (
                                <div key={`${option.option_name}_${idx}`} className='optionItem'>
                                    <span>{optionItem.option_item_order + 1}</span>
                                    <span>{optionItem.option_item_name}</span>
                                    <span>
                                        <input
                                            type="checkbox"
                                            className='checkBox'
                                            checked={optionItem.option_item_isChecked}
                                            onChange={() => { handlePersonalOptionUseChange(index, idx) }}

                                        />

                                    </span>
                                    <span>
                                        <input
                                            type="radio"
                                            className='radioButton'
                                            name={option.option_name}
                                            checked={optionItem.option_item_isDefault}
                                            onChange={() => { handlePersonalOptionDefaultChange(index, idx) }}

                                        />
                                    </span>

                                </div>
                            ))}
                        </div>
                    </article>
                ))}


                {/* 엑스트라 옵션 */}
                <div>
                    <h1>
                        엑스트라 옵션
                    </h1>
                </div>
                {showOption && extraOption.map((option: any, index: any) => (
                    <article key={option.option_name} className='optionSection'>
                        <div className='optionHeader'>
                            <h2>{option.option_name}</h2>
                            <div className='optionHeaders'>
                                <span>옵션번호</span>
                                <span>옵션명</span>
                                <span>옵션 사용여부</span>
                                <span>기본값 여부</span>
                            </div>
                        </div>
                        {option.options.map((optionItem: any, idx: any) => (
                            <div key={`${option.option_name}_${idx}`} className='optionItem'>
                                <span>{optionItem.option_item_order + 1}</span>
                                <span>{optionItem.option_item_name}</span>
                                <span>
                                    <input
                                        type="checkbox"
                                        className='checkBox'
                                        checked={optionItem.option_item_isChecked}
                                        onChange={() => { handleExtraOptionUseChange(index, idx) }}

                                    />

                                </span>
                                <span>
                                    <input
                                        type="radio"
                                        className='radioButton'
                                        name={option.option_name}
                                        checked={optionItem.option_item_isDefault}
                                        onChange={() => { handleExtraOptionDefaultChange(index, idx) }}

                                    />
                                </span>

                            </div>
                        ))}
                    </article>
                ))}
            </section>

        </div>

    );
};

export default SettingProductCreateOption;