
import { useRecoilState, useRecoilValue } from 'recoil';
import { menuInfoState, addMenuBasketState, basicOptionPriceState, personalOptionPriceState, extraOptionPriceState } from '../../../atoms/recoil';
import { useState, useEffect } from 'react';
interface CenterProps {
    setShowExtraOption: (value: boolean) => void;
    setShowPersonalOption: (value: boolean) => void;
}

const Center = ({ setShowPersonalOption, setShowExtraOption }: CenterProps) => {
    const [menuInfo, setMenuInfo] = useRecoilState(menuInfoState);
    const [addMenuBasket, setAddMenuBasket] = useRecoilState(addMenuBasketState);
    const [basicOptionPrice, setBasicOptionPrice] = useRecoilState(basicOptionPriceState);
    const [personalOptionPrice, setPersonalOptionPrice] = useRecoilState(personalOptionPriceState);
    const [extraOptionPrice, setExtraOptionPrice] = useRecoilState(extraOptionPriceState);

    const [showOptions, setShowOptions] = useState({});
    const [basicOptionCode, setBasicOptionCode] = useState<string>("");

    useEffect(() => {
        setBasicOptionCode(addMenuBasket.menuOptionCode.split('-')[0]);
    }, [addMenuBasket.menuOptionCode]);

    useEffect(() => {
        const basicOptions: any = {};
        let optionPrice: number = 0;

        // BASIC 옵션
        const updateBasicOption = () => {
            if (menuInfo === null) return;
            Object.entries(menuInfo.options.basic).map(([optionName, optionItem], index: number) => {
                // 옵션 아이템을 조회
                const OptionItem = optionItem.find(item => item.option_item_order === Number(basicOptionCode[index]));
                optionPrice += optionItem?.find(item => item.option_item_order === Number(basicOptionCode[index]))?.option_item_price || 0;
                basicOptions[optionName] = OptionItem;
            });
            return basicOptions;
        };
        const updatedBasicOptions: any = updateBasicOption();

        setAddMenuBasket((prevState: any) => ({
            ...prevState,
            options: {
                ...prevState.options, // 기존의 personal과 extra도 그대로 유지
                basic: updatedBasicOptions // 새로 업데이트된 basic 값
            },
            menuPrice: prevState.basePrice + basicOptionPrice + personalOptionPrice + extraOptionPrice,
            totalPrice: (prevState.basePrice + basicOptionPrice + personalOptionPrice + extraOptionPrice) * prevState.menuCount

        }));

    }, [basicOptionCode]);





    if (!menuInfo) {
        return <div>Loading or no menu information available...</div>;
    }


    async function changeTemperature(changeOptionIndex: number) {
        setAddMenuBasket((prevState: any) => {
            const parts = prevState.menuOptionCode.split('-');
            const firstArray = parts[0].split('');
            firstArray[0] = changeOptionIndex.toString();
            parts[0] = firstArray.join('');
            return {
                ...prevState,
                menuOptionCode: parts.join('-')
            };
        });
    }

    async function changeSize(changeOptionIndex: number) {
        setAddMenuBasket((prevState: any) => {
            const parts = prevState.menuOptionCode.split('-');
            const basicArray = parts[0].split('');
            basicArray[1] = changeOptionIndex.toString();
            parts[0] = basicArray.join('');
            return {
                ...prevState,
                menuOptionCode: parts.join('-')
            };
        });
    }

    return (
        <div className='Center'>
            {Object.entries(menuInfo.options.basic).map(([optionName, optionItem]) => {
                if (optionItem.length === 1 && optionItem[0].option_item_order === 0) {
                    return null;
                }
                return (
                    <section key={optionName}>

                        {optionName === "HOT / ICED" ?
                            <div className='basicOptions'>
                                <h1>{optionName}</h1>
                                <div className='basicOption'>
                                    {Object.entries(optionItem).map(([key, value]) => (

                                        <div className="option" key={optionName + key}>

                                            {value.option_item_name === "HOT" ?
                                                <div>
                                                    {value.option_item_order === Number(basicOptionCode[0]) ?
                                                        <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/hot_pick.png`} alt="" /> :
                                                        <img
                                                            src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/hot.png`}
                                                            alt=""
                                                            onClick={() => changeTemperature(value.option_item_order)} />
                                                    }
                                                </div>
                                                :
                                                <div>
                                                    {value.option_item_order === Number(basicOptionCode[0]) ?
                                                        <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/iced_pick.png`} alt="" /> :
                                                        <img
                                                            src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/iced.png`}
                                                            onClick={() => { changeTemperature(value.option_item_order) }}
                                                            alt="" />
                                                    }
                                                </div>}

                                        </div>
                                    ))}
                                </div>
                            </div>
                            :
                            <div className='basicOptions'>
                                <h1>{optionName}</h1>
                                <div className='basicOption'>
                                    {Object.entries(optionItem).map(([key, value]) => (

                                        <div className="option" key={optionName + key}>

                                            {(() => {
                                                switch (value.option_item_name) {
                                                    case "8oz":
                                                        return <div>
                                                            {value.option_item_order === Number(basicOptionCode[1]) ?
                                                                <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_s_pick.png`} alt="" /> :
                                                                <img
                                                                    src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_s.png`}
                                                                    alt=""
                                                                    onClick={() => { changeSize(value.option_item_order) }}
                                                                />
                                                            }
                                                        </div>
                                                    case "12oz":
                                                        return <div>
                                                            {value.option_item_order === Number(basicOptionCode[1]) ?
                                                                <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_m_pick.png`} alt="" /> :
                                                                <img
                                                                    src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_m.png`}
                                                                    alt=""
                                                                    onClick={() => { changeSize(value.option_item_order) }}
                                                                />
                                                            }
                                                        </div>
                                                    case "16oz":
                                                        return <div>
                                                            {value.option_item_order === Number(basicOptionCode[1]) ?
                                                                <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_l_pick.png`} alt="" /> :
                                                                <img
                                                                    src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_l.png`}
                                                                    alt=""
                                                                    onClick={() => { changeSize(value.option_item_order) }}
                                                                />
                                                            }
                                                        </div>
                                                    case "20oz":
                                                        return <div>
                                                            {value.option_item_order === Number(basicOptionCode[1]) ?
                                                                <img src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_xl_pick.png`} alt="" /> :
                                                                <img
                                                                    src={`https://${process.env.REACT_APP_SERVER_IP}/Content/Icon/size_xl.png`}
                                                                    alt=""
                                                                    onClick={() => { changeSize(value.option_item_order) }}
                                                                />
                                                            }
                                                        </div>
                                                    default:
                                                        return null; // 아무것도 반환하지 않음
                                                }
                                            })()}

                                        </div>
                                    ))}
                                </div>
                            </div>
                        }
                    </section>
                );
            })}
            {/* 퍼스널 옵션 진입 */}
            <section className='optionShow' onClick={() => {
                setShowPersonalOption(true);
            }}>
                <div className='name'>
                    <h1>퍼스널 옵션</h1>
                </div>
                <article>
                    <div className='price'>
                        {'\u20A9'}&nbsp;1234
                    </div>
                    <div className='button'>
                        <button className='optionType'>
                            퍼스널 옵션 선택
                        </button>
                        <div className='icon'>
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                </article>
            </section>
            {/* 엑스트라 옵션 진입*/}
            <section className='optionShow' onClick={() => {
                setShowExtraOption(true);
            }}>
                <div className='name'>
                    <h1>엑스트라 옵션</h1>
                </div>
                <article>
                    <div className='price'>
                        {'\u20A9'}&nbsp;1234
                    </div>
                    <div className='button'>
                        <button className='optionType'>
                            엑스트라 옵션 선택
                        </button>
                        <div className='icon'>
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                </article>
            </section>
        </div>
    );
};

export default Center;