import { useState, useEffect } from "react";
import axios from "axios";

const SettingCategoryMenuList = () => {
    const [menuCategoryList, setMenuCategoryList] = useState<any>([]);
    const [showMenuCategoryList, setShowMenuCategoryList] = useState(false); // 추가된 상태


    async function getMenuCategory() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMenuCategory`);
            if (response.status === 200) {
                console.log(response.data);
                setMenuCategoryList(response.data);
            }
        } catch (error) {
            console.error(error);
        }
    }

    async function deleteMenuCategory(categoryName: string) {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/deleteMenuCategory`, {
                settingPassword: process.env.REACT_APP_SETTING_PASSWORD,
                deleteCategoryName: categoryName
            });
            if (response.status === 200) {
                alert("삭제완료");
                getMenuCategory(); // 삭제 후 다시 목록을 가져옴
            }
        } catch (error) {
            console.error(error);
        }
    }


    useEffect(() => {
        if (showMenuCategoryList) { // showSubCategoryList 상태가 true일 때만 목록을 가져옴
            getMenuCategory();
        }
    }, [showMenuCategoryList]);


    return (
        <section className='SettingCategoryMenuList'>

            <h1 className='bg-gray-300'>소분류 카테고리 목록(수정기능 구현 필요)</h1>
            <p className="">
                <button
                    className='bg-gray-200'
                    onClick={() => setShowMenuCategoryList(!showMenuCategoryList)}>
                    소분류(메뉴) 카테고리 목록
                </button>
            </p>
            {showMenuCategoryList && (
                <article>
                    {menuCategoryList.map((item: any) => (
                        <div key={`${item.major_category_name}-${item.sub_category_name}-${item.menu_category_name}`} className='w-full flex justify-between items-center border border-black'>
                            <div className="flex">
                                <p>
                                    대분류 : {item.major_category_name}<br />
                                    소분류 : <b style={{ padding: 0 }}>{item.menu_category_name}</b>
                                </p>
                            </div>
                            <div className="">
                                <button className="bg-gray-200">수정</button>
                                <button className="bg-gray-200" onClick={() => deleteMenuCategory(item.menu_category_name)}>삭제</button>
                            </div>
                        </div>
                    ))}
                </article>
            )}
        </section>
    );
};

export default SettingCategoryMenuList;