import { useState, useEffect } from 'react';
import axios from 'axios';

const SettingOptionAdd = () => {
    interface OptionItem {
        name: string;
        price: number;
        details: DetailItem[]; // 세부항목 배열 추가
    }
    interface DetailItem {
        detailName: string;
        additionalCost: number;
    }
    interface Option {
        optionName: string;
        optionType: string;
        items: OptionItem[];

    }

    // 옵션 추가용 State
    const [addOptions, setAddOptions] = useState<any>([]);
    async function createOption() {
        try {
            if (!(addOptions[0].optionName.length > 0) || !(addOptions[0].items[0].name.length > 0)) {
                alert("비어있는 값을 입력해주세요");
                return;
            };
            // console.log(addOptions);
            // console.log(addOptions[0].items[0].name.length);
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createOption`, {
                settingPassword: "6195",
                options: JSON.stringify(addOptions)
            });
            if (response.status == 200) {
                alert("옵션 추가 완료");
                window.location.reload();
            }
        } catch (error: any) {
            console.error(error);
            if (error.response && error.response.status === 409) {
                alert("중복된 옵션명입니다.");
            } else {
                console.log(error);
            }
        }
    }

    // 옵션 카테고리명/ 옵션 항목 / 옵션 가격 핸들러
    const handleOptionUpdate = (optionIndex: number, itemIndex: number | null, field: string, value: any) => {
        const updatedOptions = [...addOptions];
        console.log(addOptions);
        if (field === 'optionName') {
            updatedOptions[optionIndex][field] = value;
        } else if (field === 'optionType') {
            // optionType 값이 문자열로 전달되므로 숫자로 변환
            updatedOptions[optionIndex][field] = value; // 여기서 변환
        } else if (itemIndex !== null) {
            updatedOptions[optionIndex].items[itemIndex][field] = value;
        }
        setAddOptions(updatedOptions);
    };


    const handlePriceChange = (optionIndex: number, itemIndex: number) => (e: React.ChangeEvent<HTMLInputElement>) => {
        let targetValue = parseInt(e.target.value, 10);

        // 값이 NaN이거나 0보다 작은 경우 0으로 설정
        // if (isNaN(targetValue) || targetValue < 0) {
        //     targetValue = 0;
        // }

        handleOptionUpdate(optionIndex, itemIndex, 'price', targetValue);
    };


    const handleAddOptionItem = (optionIndex: number) => {
        const updatedOptions = [...addOptions];

        updatedOptions[optionIndex].items.push({ name: '', price: 0 });
        // console.log(addOptions);
        setAddOptions(updatedOptions);
    };

    const handleRemoveOptionItem = (optionIndex: number, itemIndex: number) => {
        const updatedOptions = [...addOptions];
        updatedOptions[optionIndex].items = updatedOptions[optionIndex].items.filter((_: any, idx: any) => idx !== itemIndex);
        // 옵션 항목이 0이라면 해당 옵션 카테고리 제거
        if (updatedOptions[optionIndex].items.length === 0) {
            updatedOptions.splice(optionIndex, 1);
        }
        setAddOptions(updatedOptions);
    };

    // 옵션 추가 또는 취소
    const handleToggleOption = () => {
        if (addOptions.length > 0) {
            // 이미 추가된 옵션이 있으면 모두 제거 (취소 동작)
            setAddOptions([]);
        } else {
            // 추가된 옵션이 없으면 새 옵션 추가 (옵션 추가 동작)
            setAddOptions([{ optionName: '', optionType: 'basic', items: [{ name: '', price: 0 }] }]);
        }
    };


    // =================================================================================================

    // 세부 옵션 추가
    const handleAddDetailItem = (optionIndex: number, itemIndex: number) => {
        const updatedOptions = [...addOptions];
        if (!updatedOptions[optionIndex].items[itemIndex].details) {
            updatedOptions[optionIndex].items[itemIndex].details = []; // 세부항목 배열 초기화
        }
        updatedOptions[optionIndex].items[itemIndex].details.push({ detailName: '', additionalCost: 0 });
        setAddOptions(updatedOptions);
    };

    const handleRemoveDetailItem = (optionIndex: number, itemIndex: number, detailIndex: number) => {
        const updatedOptions = [...addOptions];
        updatedOptions[optionIndex].items[itemIndex].details = updatedOptions[optionIndex].items[itemIndex].details.filter((_: any, idx: number) => idx !== detailIndex);
        setAddOptions(updatedOptions);
    };

    const handleDetailUpdate = (optionIndex: number, itemIndex: number, detailIndex: number, field: string, value: any) => {
        const updatedOptions = [...addOptions];
        updatedOptions[optionIndex].items[itemIndex].details[detailIndex][field] = value;
        setAddOptions(updatedOptions);
    };
    return (
        <section className='SettingOptionCreate'>
            <h1 className=' bg-gray-300'>옵션 추가</h1>
            <div className=' text-right'>
                <button className='bg-gray-200' onClick={handleToggleOption}>
                    {addOptions.length > 0 ? '취소' : '옵션 추가'}
                </button>
            </div>
            {addOptions.map((option: Option, optionIndex: number) => (
                <table key={optionIndex} className=''>
                    <tbody>
                        <tr className=''>
                            <td
                                className=" text-right"
                                colSpan={2}
                            >
                                <button
                                    className='bg-gray-200'
                                    onClick={() => handleAddOptionItem(optionIndex)}
                                >
                                    항목 추가
                                </button>
                            </td>
                        </tr>
                        <tr className='optionName'>
                            <td className='' colSpan={2}>
                                <input
                                    className=''
                                    type="text"
                                    placeholder='옵션 이름'
                                    value={option.optionName}
                                    onChange={(e) => handleOptionUpdate(optionIndex, null, 'optionName', e.target.value)}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <select
                                    className=''
                                    value={option.optionType}
                                    onChange={(e) => handleOptionUpdate(optionIndex, null, 'optionType', e.target.value)}
                                >
                                    <option value="basic">basic</option>
                                    <option value="personal">personal</option>
                                    <option value="extra">extra</option>
                                </select>

                            </td>
                        </tr>
                        {option.items.map((item: OptionItem, itemIndex: number) => (
                            <tr key={itemIndex} className='optionInfo'>
                                <td className='w-2/5'>옵션 항목</td>
                                <td className='w-3/5'>
                                    <div className=''>
                                        <input
                                            className=''
                                            type="text"
                                            placeholder='옵션 항목'
                                            value={item.name}
                                            onChange={(e) => handleOptionUpdate(optionIndex, itemIndex, 'name', e.target.value)}
                                        />

                                    </div>
                                    <div className=''>
                                        <input
                                            className=''
                                            type="number"
                                            placeholder='옵션 가격'
                                            value={item.price !== undefined ? item.price : ''}
                                            onChange={handlePriceChange(optionIndex, itemIndex)}
                                        />
                                    </div>
                                    <div className='button'>

                                        <button onClick={() => handleAddDetailItem(optionIndex, itemIndex)}>
                                            세부항목 추가
                                        </button>
                                        <button
                                            onClick={() => handleRemoveOptionItem(optionIndex, itemIndex)}>
                                            삭제
                                        </button>
                                    </div>
                                    {item.details && item.details.map((detail: DetailItem, detailIndex: number) => (
                                        <div key={detailIndex} className='detailItem'>
                                            <div>
                                                <input
                                                    type="text"
                                                    placeholder='세부 옵션 이름'
                                                    value={detail.detailName}
                                                    onChange={(e) => handleDetailUpdate(optionIndex, itemIndex, detailIndex, 'detailName', e.target.value)}
                                                />
                                            </div>
                                            <div>
                                                <input
                                                    type="number"
                                                    placeholder='추가 비용'
                                                    value={detail.additionalCost === 0 ? '' : detail.additionalCost}
                                                    onChange={(e) => handleDetailUpdate(optionIndex, itemIndex, detailIndex, 'additionalCost', parseInt(e.target.value, 10))}
                                                />
                                            </div>
                                            <div>
                                                <button onClick={() => handleRemoveDetailItem(optionIndex, itemIndex, detailIndex)}>
                                                    세부 항목 삭제
                                                </button>
                                            </div>
                                        </div>
                                    ))}
                                </td>
                            </tr>
                        ))}


                    </tbody>
                </table>
            ))}

            <div>
                {addOptions.length > 0 && <button onClick={() => { createOption() }}>옵션 저장</button>}
            </div>
        </section>
    );
};

export default SettingOptionAdd;