import React, { useState, useEffect, useRef } from "react";

const WebSocketChatting = () => {
    const [messages, setMessages] = useState<Array<{ id: number, message: string }>>([]);
    const [input, setInput] = useState("");
    const [webSocket, setWebSocket] = useState<WebSocket | null>(null);

    useEffect(() => {
        const webSocket = new WebSocket("wss://nanonix.lol/VoiceOrder");
        setWebSocket(webSocket);
        webSocket.onmessage = (message) => {
            const data = JSON.parse(message.data);
            if (data.message && data.message.data) {
                // Buffer 데이터를 문자열로 변환
                const messageData = new Uint8Array(data.message.data);
                const decodedMessage = new TextDecoder("utf-8").decode(messageData);
                data.message = decodedMessage;
            }
            console.log(data);
            // 수신 메시지를 대화내역에 추가
            setMessages((prevMessages) => [...prevMessages, data]);

        };

        return () => {
            if (webSocket) {
                webSocket.close();
            }
        };
    }, []);

    const sendMessage = () => {
        if (webSocket) {
            webSocket.send(input);
            setInput("");
        }
    };

    return (
        <div>
            <input
                type="text"
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyPress={(e) => {
                    if (e.key === "Enter") {
                        sendMessage();
                    }
                }}
            />
            <button onClick={sendMessage}>Send</button>
            <div>
                <div>
                    {messages.map((msg, index) => (
                        <div key={index}>접속자{msg.id} : {msg.message}</div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default WebSocketChatting;
