import { useState, useEffect } from "react";
import axios from "axios";

const SettingCategoryMenuCreate = () => {
    const [createMenuCategoryName, setCreateMenuCategoryName] = useState("");
    const [majorCategoryName, setMajorCategoryName] = useState("");
    const [majorCategoryList, setMajorCategoryList] = useState([]);


    async function getMajorCategory() {
        try {
            if (majorCategoryList.length === 0) {
                const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMajorCategory`);
                if (response.status === 200) {
                    setMajorCategoryList(response.data);
                }
            } else {
                setMajorCategoryList([]);
            }
        } catch (error) {
            console.error('Error:', error);
        }
    }
    async function createMenuCategory() {
        try {
            // 입력된 카테고리 이름의 유효성 검사
            if (!(createMenuCategoryName.length > 0)) {
                alert("값을 입력해주세요");
                return;
            };

            // 대분류 카테고리 선택 여부 검사
            if (!majorCategoryName) {
                alert("대분류를 선택해주세요");
                return;
            }

            // 소분류 카테고리 생성 요청
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createMenuCategory`, {
                settingPassword: "6195",
                majorCategoryName: majorCategoryName,
                createMenuCategoryName: createMenuCategoryName
            });
            if (response.status === 200) {
                console.log(response.data);
                alert("카테고리 추가 완료");
                window.location.reload();
            }

        } catch (error: any) {
            console.log(error);
            if (error.response && error.response.status === 409) {
                alert("중복된 카테고리입니다.");
            } else {
                console.log(error);
            }
        }
    }

    const handleLabelClick = (item: string) => {
        // 라디오 버튼의 상태를 업데이트합니다.
        setMajorCategoryName(item);
    };

    useEffect(() => {
        console.log(majorCategoryName);
        console.log(createMenuCategoryName);
    }, [majorCategoryName, majorCategoryList]);


    return (
        <section className='SettingCategoryMenuCreate'>
            <h1 className="bg-gray-300">소분류(메뉴) 카테고리 추가</h1>
            <table className='w-full'>
                <tbody>
                    <tr className=''>
                        <td className='w-2/5'>추가할 카테고리 명칭</td>
                        <td className='w-3/5'>
                            <input
                                className='w-full border border-black'
                                type="text"
                                placeholder='소분류 카테고리명'
                                value={createMenuCategoryName}
                                onChange={(e) => setCreateMenuCategoryName(e.target.value)} />
                        </td>
                    </tr>
                    <tr className='w-full'>
                        <td className='w-2/5'>참조할 중분류 카테고리</td>
                        <td className='w-3/5'>
                            <button
                                className="bg-gray-200 w-full"
                                onClick={getMajorCategory}>
                                {majorCategoryList.length === 0 ? "카테고리 보기" : "카테고리 숨기기"}
                            </button>
                            {majorCategoryList.map((item: any, index: number) => (
                                <div className='border border-black' key={item.major_category_name}>
                                    <input
                                        type="radio"
                                        value={item.major_category_name}
                                        checked={item.major_category_name === majorCategoryName}
                                        onChange={(e) => setMajorCategoryName(e.target.value)}
                                    />
                                    &nbsp;
                                    <button
                                        className='w-2/3 text-left'
                                        onClick={() => handleLabelClick(item.major_category_name)}> {/* 수정된 부분 */}
                                        {item.major_category_name}
                                    </button>
                                </div>
                            ))}
                        </td>
                    </tr>
                    <tr className='w-full text-right'>
                        <td colSpan={2} style={{ padding: 0}}>
                            <button
                                className="bg-gray-200"
                                onClick={createMenuCategory}>소분류 카테고리 추가
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </section>
    );
};

export default SettingCategoryMenuCreate;