// Settings.js
import { useState, useEffect } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import axios from 'axios';

import SettingHeader from './SettingHeader';
import SettingList from './List/SettingList';
import SettingCategory from './Category/SettingCategory';
import SettingBranch from './Branch/SettingBranch';
import SettingOption from './Option/SettingOption';
import SettingProduct from './Product/SettingProduct';
import "./Settings.css";

const Settings = () => {
    const [isAllowed, setIsAllowed] = useState(false);

    const [password, setPassword] = useState('');

    const checkPassword = async () => {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/checkPassword`, {
                password : password
            });
            if (response.data === true) {
                console.log(response.data);
                setIsAllowed(response.data);
            } else {
                console.log(response.data);
                setIsAllowed(response.data);
            }
        } catch (error) {
            console.error('Error verifying password:', error);
            setIsAllowed(false);
        }
    };

    const checkSession = async () => {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/checkSession`);
            console.log(response.data);
            setIsAllowed(response.data);
        } catch (error) {
            console.error('Session check failed:', error);
            setIsAllowed(false);
        }
    };
    
    //세션 체크
    // useEffect(() => {
    //     const interval = setInterval(() => {
    //         checkSession();
    //     }, 30000); // 매 1초마다 세션 확인
    
    //     return () => clearInterval(interval); // 컴포넌트 언마운트 시 인터벌 해제
    // }, []);
    // 세션 미체크시 div
    // if (!isAllowed) {
    //     return (
    //         <div>
    //             <form onSubmit={(e) => { e.preventDefault(); checkPassword(); }}>
    //                 <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
    //                 <button type="submit">Submit</button>
    //             </form>
    //         </div>
    //     );
    // }

    return (
        <section className='Settings'>
            <SettingHeader />
            <Routes>
                <Route path="SettingList" element={<SettingList />} />
                <Route path="SettingCategory" element={<SettingCategory />} />
                <Route path="SettingBranch" element={<SettingBranch />} />
                <Route path="SettingOption" element={<SettingOption />} />
                <Route path="SettingProduct" element={<SettingProduct />} />
            </Routes>
        </section>
    );
};

export default Settings;
