import axios from 'axios';
import { useState, useEffect } from 'react';

const SettingOptionList = () => {
    interface ShowOptions {
        [key: string]: boolean;
        basic: boolean,
        personal: boolean,
        extra: boolean
    }
    const [showOptions, setShowOptions] = useState<ShowOptions>({
        basic: false,
        personal: false,
        extra: false
    });
    const [basicOption, setBasicOption] = useState<any>([]);
    const [personalOption, setPersonalOption] = useState<any>([]);
    const [extraOption, setExtraOption] = useState<any>([]);

    // ==============================
    // 수정
    const [editStatus, setEditStatus] = useState<any>({});
    const [optionValues, setOptionValues] = useState<any>({});

    // 옵션 상태 업데이트 함수
    const handleOptionChange = (optionName: any, optionOrder: number, optionItemName: string, optionItemPrice: number) => {
        setOptionValues((prevState: any) => {
            const optionCopy = prevState[optionName]?.[optionOrder] ? { ...prevState[optionName][optionOrder] } : {};
            optionCopy.option_item_name = optionItemName;
            optionCopy.option_item_price = optionItemPrice;

            // details 배열이 없는 경우 빈 배열을 추가합니다.
            if (!optionCopy.details) {
                optionCopy.details = [];
            }

            return {
                ...prevState,
                [optionName]: {
                    ...prevState[optionName],
                    [optionOrder]: optionCopy
                }
            };
        });
    };

    const handleDetailOptionChange = (optionName: any, optionOrder: number, detailIndex: number, detailItemName?: string, detailItemPrice?: number) => {
        setOptionValues((prevState: any) => {
            const detailsCopy = prevState[optionName][optionOrder].details.slice();
            detailsCopy[detailIndex] = {
                option_item_detail_name: detailItemName !== undefined ? detailItemName : detailsCopy[detailIndex].option_item_detail_name,
                additionalCost: detailItemPrice !== undefined ? detailItemPrice : detailsCopy[detailIndex].additionalCost
            };

            return {
                ...prevState,
                [optionName]: {
                    ...prevState[optionName],
                    [optionOrder]: {
                        ...prevState[optionName][optionOrder],
                        details: detailsCopy
                    }
                }
            };
        });
    };

    const toggleEdit = (optionName: any, options: any) => {
        if (editStatus[optionName]) {
            setEditStatus((prevState: any) => ({ ...prevState, [optionName]: false }));
            setOptionValues({});
            return;
        }
        // 모든 옵션의 수정 상태를 초기화하고, 현재 옵션만 수정 상태로 설정
        setEditStatus({ [optionName]: true });

        // 모든 옵션의 값들을 초기화하고, 현재 옵션의 초기 값만 설정
        setOptionValues({
            [optionName]: options.reduce((acc: any, item: any) => ({
                ...acc,
                [item.option_item_order]: {
                    option_item_name: item.option_item_name,
                    option_item_price: item.option_item_price,
                    details: item.details
                }
            }), {})
        });
    };

    const editOption = async () => {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/editOption`, {
                editOption: optionValues
            });
            if (response.status === 200) {
                console.log(response.data);
                window.location.reload();

            }
        } catch (error) {
            console.log(error);
        }

    }

    useEffect(() => {
        console.log(optionValues);
    }, [optionValues]);
    // ==============================

    useEffect(() => {
        console.log(basicOption);
    }, [basicOption]);
    useEffect(() => {
        console.log(personalOption);

    }, [personalOption]);
    useEffect(() => {
        console.log(extraOption);
        console.log("====================================");
        // console.log(extraOption[2].options[1]);
    }, [extraOption]);

    async function getOption() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getOption`);
            if (response.status == 200) {
                console.log(response.data);
                const data = response.data;

                // 유형별로 분류
                const basicOptions = data.filter((item: any) => item.option_type === 'basic').sort((a: any, b: any) => a.option_order - b.option_order);
                const personalOptions = data.filter((item: any) => item.option_type === 'personal').sort((a: any, b: any) => a.option_order - b.option_order);
                const extraOptions = data.filter((item: any) => item.option_type === 'extra').sort((a: any, b: any) => a.option_order - b.option_order);

                // 각 상태 업데이트
                setBasicOption(basicOptions);
                setPersonalOption(personalOptions);
                setExtraOption(extraOptions);
            }
        } catch (error) {
            console.error(error);
        }
    };


    useEffect(() => {
        getOption();
    }, []);


    function handleShowOptions(optionType: string) {
        setShowOptions((prevState) => ({
            ...prevState,
            [optionType]: !prevState[optionType]
        }));
    }

    return (
        <div className='SettingOptionList'>
            <h1>옵션 리스트</h1>


            {/* =========================================================== */}
            {/* 기본 옵션 */}
            {/* =========================================================== */}
            {/* {showOptions && ( */}
            <section className='basicOption'>
                <div className='title'>
                    <h1>Basic 옵션</h1>
                </div>
                {showOptions.basic ? <button onClick={() => { handleShowOptions("basic") }}>숨기기</button> : <button onClick={() => { handleShowOptions("basic") }}>보기</button>}
                {showOptions.basic && basicOption.map((item: any, index: number) => (
                    <article key={item.option_name} className='option'>
                        <div className='option_name'>
                            <h2>{item.option_name}</h2>
                            <button onClick={() => toggleEdit(item.option_name, item.options)}>
                                {editStatus[item.option_name] ? <div onClick={() => {editOption()}}>저장</div> : <div>수정</div>}
                            </button>
                        </div>
                        <article className='name'>
                            <div>옵션번호</div>
                            <div>옵션항목</div>
                            <div>가격</div>
                            <div>세부항목</div>
                            {/* <div>비고</div> */}
                        </article>

                        {item.options.map((option: any, index: number) => (
                            <div className='items' key={option.option_item_name + index}>
                                <div>{option.option_item_order + 1}</div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_name !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_name : option.option_item_name}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, e.target.value, optionValues[item.option_name]?.[option.option_item_order].option_item_price || option.option_item_price)}
                                        />
                                    ) : (
                                        option.option_item_name
                                    )}
                                </div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_price !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_price : option.option_item_price}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, optionValues[item.option_name]?.[option.option_item_order]?.option_item_name || option.option_item_name, Number(e.target.value))}
                                        />
                                    ) : (
                                        option.option_item_price
                                    )}
                                </div>
                                <div>&nbsp;</div>
                            </div>
                        ))}
                    </article>
                ))}
                {/* )} */}
            </section>
            {/* =========================================================== */}
            {/* 퍼스널 옵션 */}
            {/* =========================================================== */}
            <section className='basicOption'>
                <div className='title'>
                    <h1>Personal 옵션</h1>
                </div>
                {extraOption.personal ? <button onClick={() => { handleShowOptions("personal") }}>숨기기</button> : <button onClick={() => { handleShowOptions("personal") }}>보기</button>}
                {showOptions.personal && personalOption.map((item: any, index: number) => (
                    <article key={item.option_name} className='option'>
                        <div className='option_name'>
                            <h2>{item.option_name}</h2>
                            <button onClick={() => toggleEdit(item.option_name, item.options)}>
                            {editStatus[item.option_name] ? <div onClick={() => {editOption()}}>저장</div> : <div>수정</div>}
                            </button>
                        </div>
                        <article className='name'>
                            <div>옵션번호</div>
                            <div>옵션항목</div>
                            <div>가격</div>
                            <div>세부항목</div>
                        </article>

                        {item.options.map((option: any, index: number) => (
                            <div className='items' key={item.option_name + option.option_item_name}>
                                <div>{option.option_item_order + 1}</div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_name !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_name : option.option_item_name}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, e.target.value, optionValues[item.option_name]?.[option.option_item_order].option_item_price || option.option_item_price)}
                                        />
                                    ) : (
                                        option.option_item_name
                                    )}
                                </div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_price !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_price : option.option_item_price}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, optionValues[item.option_name]?.[option.option_item_order]?.option_item_name || option.option_item_name, Number(e.target.value))}
                                        />
                                    ) : (
                                        option.option_item_price
                                    )}
                                </div>
                                <div>&nbsp;</div>
                            </div>
                        ))}
                    </article>
                ))}
            </section>
            {/* =========================================================== */}
            {/* 엑스트라 옵션 */}
            {/* =========================================================== */}
            <section className='basicOption'>
                <div className='title'>
                    <h1>Extra 옵션</h1>
                </div>
                {extraOption.extra ? <button onClick={() => { handleShowOptions("extra") }}>숨기기</button> : <button onClick={() => { handleShowOptions("extra") }}>보기</button>}
                {showOptions.extra && extraOption.map((item: any, index: number) => (
                    <article key={item.option_name} className='option'>
                        <div className='option_name'>
                            <h2>{item.option_name}</h2>
                            <button onClick={() => toggleEdit(item.option_name, item.options)}>
                            {editStatus[item.option_name] ? <div onClick={() => {editOption()}}>저장</div> : <div>수정</div>}
                            </button>
                        </div>
                        <article className='name'>
                            <div>옵션번호</div>
                            <div>옵션항목</div>
                            <div>가격</div>
                            <div>세부항목</div>
                        </article>

                        {item.options.map((option: any, index: number) => (
                            <div className='items' key={option.option_item_name + index}>
                                <div>{option.option_item_order + 1}</div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_name !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_name : option.option_item_name}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, e.target.value, optionValues[item.option_name]?.[option.option_item_order].option_item_price || option.option_item_price)}
                                        />
                                    ) : (
                                        option.option_item_name
                                    )}
                                </div>
                                <div>
                                    {editStatus[item.option_name] ? (
                                        <input
                                            type="text"
                                            value={optionValues[item.option_name]?.[option.option_item_order]?.option_item_price !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.option_item_price : option.option_item_price}
                                            onChange={(e) => handleOptionChange(item.option_name, option.option_item_order, optionValues[item.option_name]?.[option.option_item_order]?.option_item_name || option.option_item_name, Number(e.target.value))}
                                        />
                                    ) : (
                                        option.option_item_price
                                    )}
                                </div>
                                <div>
                                    {option.details.map((detail: any, dIndex: number) => (
                                        <div className="detail" key={item.option_name + "_" + detail.option_item_detail_name}>
                                            {editStatus[item.option_name] ? (
                                                <input
                                                    type="text"

                                                    value={optionValues[item.option_name]?.[option.option_item_order]?.details[dIndex].option_item_detail_name !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.details[dIndex].option_item_detail_name : detail.option_item_detail_name}
                                                    onChange={(e) => handleDetailOptionChange(item.option_name, option.option_item_order, dIndex, e.target.value)}
                                                />
                                            ) : (
                                                <div>
                                                    {detail.option_item_detail_name}
                                                </div>
                                            )}
                                            {editStatus[item.option_name] ? (
                                                <input
                                                    type="text"
                                                    value={optionValues[item.option_name]?.[option.option_item_order]?.details[dIndex].additionalCost !== undefined ? optionValues[item.option_name]?.[option.option_item_order]?.details[dIndex].additionalCost : detail.additionalCost}
                                                    onChange={(e) => handleDetailOptionChange(item.option_name, option.option_item_order, dIndex, undefined, Number(e.target.value))}
                                                />
                                            ) : (
                                                <div>
                                                    {detail.additionalCost}
                                                </div>
                                            )}
                                        </div>
                                    ))}
                                </div>
                            </div>
                        ))}
                    </article>
                ))}
            </section>
            {/* =========================================================== */}
        </div>
    );
};

export default SettingOptionList;