import { useRecoilState, useRecoilValue } from 'recoil';
import { menuInfoState, addMenuBasketState, basicOptionPriceState, personalOptionPriceState, extraOptionPriceState } from '../../../atoms/recoil';
import { useState, useEffect } from 'react';
interface PersonalOptionProps {
    personalOptionClose: () => void;
}
const PersonalOption = ({ personalOptionClose }: PersonalOptionProps) => {
    const [menuInfo, setMenuInfo] = useRecoilState(menuInfoState);
    const [addMenuBasket, setAddMenuBasket] = useRecoilState(addMenuBasketState);
    const [basicOptionPrice, setBasicOptionPrice] = useRecoilState(basicOptionPriceState);
    const [personalOptionPrice, setPersonalOptionPrice] = useRecoilState(personalOptionPriceState);
    const [extraOptionPrice, setExtraOptionPrice] = useRecoilState(extraOptionPriceState);
    const [personalOptionCode, setPersonalOptionCode] = useState<string>("");



    useEffect(() => {
        const personalOptions: any = {};
        let optionPrice: number = 0;

        // BASIC 옵션
        const updatePersonalOption = () => {
            if (menuInfo === null) return;
            Object.entries(menuInfo.options.personal).map(([optionName, optionItem], index: number) => {
                // 옵션 아이템을 조회
                const OptionItem = optionItem.find(item => item.option_item_order === Number(personalOptionCode[index]));
                optionPrice += optionItem?.find(item => item.option_item_order === Number(personalOptionCode[index]))?.option_item_price || 0;
                personalOptions[optionName] = OptionItem;
            });
            return personalOptions;
        };
        const updatedPersonalOptions: any = updatePersonalOption();
        setBasicOptionPrice(optionPrice);
        setAddMenuBasket((prevState: any) => ({
            ...prevState,
            options: {
                ...prevState.options,
                personal: updatedPersonalOptions
            },
            menuPrice: prevState.basePrice + basicOptionPrice + personalOptionPrice + extraOptionPrice,
            totalPrice: (prevState.basePrice + basicOptionPrice + personalOptionPrice + extraOptionPrice) * prevState.menuCount

        }));
    }, [personalOptionCode]);

    useEffect(() => {
        setPersonalOptionCode(addMenuBasket.menuOptionCode.split('-')[1]);
    }, [addMenuBasket.menuOptionCode]);

    if (!menuInfo) {
        return <div>Loading or no menu information available...</div>;
    }

    async function changePersonalOptions(optionIndex:number, optionItemOrder: number) {
        setAddMenuBasket((prevState: any) => {
            const parts = prevState.menuOptionCode.split('-');
            const personalArray = parts[1].split('');
            personalArray[optionIndex] = optionItemOrder.toString();
            parts[1] = personalArray.join('');
            return {
                ...prevState,
                menuOptionCode: parts.join('-')
            };
        });
    }

    return (
        <div className='PersonalOption'>
            <div className='personalTitle'>
                <h1>퍼스널 옵션</h1>
                <div className='' onClick={() => { personalOptionClose() }}>
                    <span></span>
                    <span></span>
                </div>
            </div>
            {Object.entries(menuInfo.options.personal).map(([optionName, optionItem], index:number) => {
                // console.log(optionItem);
                if (optionItem.length === 1 && optionItem[0].option_item_order === 0) {
                    return null;
                }
                return (
                    <section className="personalOptions" key={optionName}>

                        {optionName === "~" ?
                            <div className='personalOptionItems'>
                                <h1>{optionName}</h1>

                            </div>
                            :
                            <div className='personalOptionItems'>
                                <h1>{optionName}</h1>
                                <div className='personalOptionItem'>
                                    {Object.entries(optionItem).map(([key, value], idx:number) => (
                                        <div className="items"key={key}> {/* key 추가 */}
                                            {value.option_item_order === Number(personalOptionCode[index]) ?
                                                <div className='pick' onClick={() => {changePersonalOptions(index, value.option_item_order)}}>
                                                    {value.option_item_name}
                                                </div>
                                                :
                                                <div onClick={() => {changePersonalOptions(index, value.option_item_order)}}>
                                                    {value.option_item_name}
                                                </div>
                                            }
                                        </div>
                                    ))}
                                </div>
                            </div>
                        }
                    </section>
                );
            })}
        </div>
    );
};

export default PersonalOption;