import { useState } from 'react';
import axios from 'axios';

const SettingCategoryAdd = () => {
    const [majorCategoryName, setMajorCategoryName] = useState("");

    async function createMajorCategory() {
        try {
            if (!(majorCategoryName.length > 0)) {
                alert("값을 입력해주세요");
                return;
            };

            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createMajorCategory`, {
                settingPassword: process.env.REACT_APP_SETTING_PASSWORD,
                majorCategoryName: majorCategoryName
            });
            if (response.status === 200) {
                console.log(response.data);
                alert("카테고리 추가 완료");
                window.location.reload();
       
            }

        } catch (error: any) {
            console.log(error);
            if (error.response && error.response.status === 409) {
                alert("중복된 카테고리입니다.");
            } else {
                console.log(error);
            }
        }
    }
    return (
        
        <section className='SettingCategoryMajorCreate'>
            <h1 className='bg-gray-300'>대분류 카테고리 추가</h1>
            <table className='w-full'>
                <tbody>
                    <tr className='w-full'>
                        <td className='w-2/5'>추가할 카테고리 명칭</td>
                        <td className='w-3/5'>
                            <input 
                                className='border border-black w-full'
                                type="text"
                                placeholder='카테고리명'
                                value={majorCategoryName}
                                onChange={(e) => setMajorCategoryName(e.target.value)}
                            />
                        </td>
                    </tr>
                    <tr className='w-full'>
                        <td colSpan={2} className='text-right'>
                            <button
                                className='bg-gray-200'
                                onClick={createMajorCategory}>대분류 카테고리 추가
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </section>
    );
};

export default SettingCategoryAdd;