import { useState, useEffect } from 'react';
import axios from 'axios';
const SettingListProduct = () => {
    const [choiceMajorCategory, setChoiceMajorCategory] = useState<string>("")
    const [majorCategoryList, setMajorCategoryList] = useState<any>([]);
    const [choiceMenuCategory, setChoiceMenuCategory] = useState<string>("")
    const [menuCategoryList, setMenuCategoryList] = useState<any>([]);
    const [menuList, setMenuList] = useState<any | null>([]);

    // 대분류 받기
    async function getMajorCategory() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMajorCategory`);
            if (response.status == 200) {
                // console.log(response.data);
                setMajorCategoryList(response.data);
                setChoiceMajorCategory(response.data[0].major_category_name);
                return response.data[0].major_category_name;
            }
        } catch (error) {
            console.error(error);
        }
    }


    // 소분류 받기
    async function getMenuCategoryByMajorCategory(majorCategoryName: string) {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMenuCategoryByMajorCategory?majorCategoryName=${majorCategoryName}`);
            if (response.status == 200) {
                console.log(response.data);
                setMenuCategoryList(response.data);
                setChoiceMenuCategory(response.data[0].menu_category_name);
                return response.data[0].menu_category_name;
            }
        } catch (error) {
            console.error(error);
        }
    }


    // 메뉴받기
    async function getMenu(menuCategoryName: string) {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMenu?menuCategoryName=${menuCategoryName}`);
            if (response.status == 200) {
                // console.log(response.data);
                setMenuList(response.data);
                

            }
        } catch (error) {
            console.error(error);
        }
    }
    async function deleteMenu(deleteMenuName: string) {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/deleteMenu`, {
                settingPassword: "6195",
                deleteMenuName: deleteMenuName
            });
            if (response.status === 200) {
                window.location.reload();
                alert("삭제완료");
                // console.log("삭제완료");
            }
        } catch (error) {
            console.error(error)
        }
    }
    async function getOptionByMenu(menuName: string) {
        console.log(menuName);
        try{
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Product/getOptionByMenu?menuName=${menuName}`);
            if(response.status === 200){
                console.log(response.data);
            }
        }catch(error){
            console.error(error);
        }
    }
    // 페이지 로드
    useEffect(()=>{
        getMajorCategory();
    }, [])

    // 대분류 선택
    useEffect(()=>{
        if(choiceMajorCategory) getMenuCategoryByMajorCategory(choiceMajorCategory);
    }, [choiceMajorCategory]);

    // // 소분류  선택
    useEffect(()=>{
        if(choiceMenuCategory) getMenu(choiceMenuCategory);
    }, [choiceMenuCategory]);


    return (
        <section className='SettingListProduct'>
            <h1>상품 카테고리 선택</h1>
            {/* 대분류 */}
            <article className="flex border border-black">
                {majorCategoryList && majorCategoryList.map((item: any) => (
                    <button
                        className={`bg-gray-200 mr-2`}
                        key={item.major_category_name}
                        onClick={() => { setChoiceMajorCategory(item.major_category_name) }}
                        style={{ backgroundColor: item.major_category_name === choiceMajorCategory ? 'rgb(156 163 175)' : '' }}
                    >
                        {item.major_category_name}
                    </button>
                ))}
            </article>

            {/* 소분류 */}
            <article className="flex border border-black">
                {menuCategoryList.map((item: any, index: number) => (
                    <button
                        className='bg-gray-200 mr-2'
                        key={`${index}${item.menu_category_name}`}
                        onClick={() => { setChoiceMenuCategory(item.menu_category_name) }}
                        style={{ backgroundColor: item.menu_category_name === choiceMenuCategory ? 'rgb(156 163 175)' : '' }}
                    >
                        {item.menu_category_name}
                    </button>
                ))}
            </article>

            <article className='menu'>
                {menuList.length > 0 ? (menuList.map((item: any) => (
                    <div key={`${item.menu_name}`} onClick={() => getOptionByMenu(item.menu_name)}>
                        <h1>{item.menu_name}</h1>
                        <img style={{width:"200px"}} src={`https://${process.env.REACT_APP_SERVER_IP}/${item.menu_imagepath}`} alt="" />
                        <p>
                            <button className='bg-red-500 text-white' onClick={() => deleteMenu(item.menu_name)}>삭제</button>
                            {item.menu_imagepath}
                        </p>
                    </div>
                ))) : (
                    <p>출력할 메뉴가 없습니다.</p>
                )}
            </article>
        </section>
    );
};

export default SettingListProduct;