import { useState, useEffect } from 'react';
import axios from 'axios';

const SettingCategoryMajorList = () => {
    const [majorCategory, setMajorCategory] = useState<any>([]);
    const [showMajorCategory, setShowMajorCategory] = useState(false); // 추가된 상태

    async function getMajorCategory() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMajorCategory`);
            if (response.status === 200) {
                console.log(response.data);
                setMajorCategory(response.data);
            }
        } catch (error) {
            console.error(error);
        }
    }

    async function deleteMajorCategory(categoryName: string) {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/deleteMajorCategory`, {
                settingPassword: "6195",
                deleteCategoryName: categoryName
            });
            if (response.status === 200) {
                alert("삭제완료");
                getMajorCategory(); // 삭제 후 다시 목록을 가져옴
            }
        } catch (error) {
            console.error(error);
        }
    }

    useEffect(() => {
        if (showMajorCategory) { // showMajorCategory 상태가 true일 때만 목록을 가져옴
            getMajorCategory();
        }
    }, [showMajorCategory]);

    return (
        <section className='SettingCategoryMajorList'>
            <h1 className='bg-gray-300'>대분류 카테고리 목록(수정기능 구현 필요)</h1>
            <p className=''>
                <button
                    className='bg-gray-200'
                    onClick={() => setShowMajorCategory(!showMajorCategory)}>대분류 카테고리 목록
                </button>
            </p>
            {showMajorCategory && (
                <article className="category">
                    {majorCategory.map((item: any) => (
                        <div key={item.major_category_name} className='flex justify-between items-center border border-black'>
                            <p className=''>카테고리 : {item.major_category_name}</p>
                            <p className='flex'>
                                <button className='border border-black'>수정</button>
                                <button className='border border-black' onClick={() => deleteMajorCategory(item.major_category_name)}>삭제</button>
                            </p>
                        </div>
                    ))}
                </article>
            )}
        </section>
    );
};

export default SettingCategoryMajorList;
