import { useRecoilState, useRecoilValue } from 'recoil';
import { menuInfoState, addMenuBasketState } from '../../../atoms/recoil';
import { useEffect } from 'react';

const Top = ({ onClose }: { onClose: () => void }) => {
    const menuInfo = useRecoilValue(menuInfoState);
    const [addMenuBasket, setAddMenuBasket] = useRecoilState(addMenuBasketState);
    useEffect(() => {
        console.log(addMenuBasket)
    }, [addMenuBasket]);
    if (!menuInfo) {
        return null;
    }

    
    // 새로운 총 가격을 계산하는 함수
    const calculateNewPrice = (newMenuCount: number) => {
        const totalPrice = addMenuBasket.menuPrice * newMenuCount;
        return totalPrice;
    };

    // 메뉴 수량 감소
    const decreaseMenuCount = () => {
        if (addMenuBasket.menuCount > 1) {
            const newMenuCount = addMenuBasket.menuCount - 1;
            const newPrice = calculateNewPrice(newMenuCount);
            setAddMenuBasket({
                ...addMenuBasket, menuCount: newMenuCount, totalPrice: newPrice
            });
        }
    };

    // 메뉴 수량 증가
    const increaseMenuCount = () => {
        const newMenuCount = addMenuBasket.menuCount + 1;
        const newPrice = calculateNewPrice(newMenuCount);
        setAddMenuBasket({ ...addMenuBasket, menuCount: newMenuCount, totalPrice: newPrice });
    };

    return (
        <div className='TOP'>
            <article className='exitButton'>
                <div onClick={() => { onClose() }}>
                    <span></span>
                    <span></span>
                </div>
            </article>
            <article className='MenuTitle'>
                <div className='imgBox'>
                    <img src={`https://${process.env.REACT_APP_SERVER_IP}/${menuInfo.menu_imagepath}`} alt="" />
                </div>
                <div className='menuCategory'>
                    <div className='menuCategoryName'>
                        <p>[{menuInfo.menu_category_name}]</p>
                    </div>
                    <div className='menuName'>
                        <p>{menuInfo.menu_name}</p>
                    </div>
                    <div className='menuCount'>
                        <div>
                            <button onClick={decreaseMenuCount}> - </button>
                            <p>{addMenuBasket.menuCount}</p>
                            <button onClick={increaseMenuCount}> + </button>
                        </div>
                    </div>
                    <div className='menuPrice'>
                        <p>
                            주문금액
                        </p>
                        <p>
                            {/* {'\u20A9'}&nbsp; */}
                            {addMenuBasket.totalPrice.toLocaleString('ko-KR')}원
                        </p>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Top;
