import { useState, useEffect } from "react";
import axios from 'axios';

const SettingBranchAdd = () => {
    const [branchName, setBranchName] = useState<any>("");
    const [branchCode, setBranchCode] = useState<any>("");


    async function createBranch() {
        try{
            if(!(branchName.length > 0)){
                alert("값을 입력해주세요");
                return;
            };
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createBranch`, {
                branchName: branchName,
                branchCode: branchCode
            });

            if(response.status === 200){
                console.log(response.data);
                alert("지점 추가 완료");
                window.location.reload();
            }
        }catch(error: any){
            console.log(error);
            if (error.response && error.response.status === 409) {
                alert("중복된 카테고리입니다.");
            } else {
                console.log(error);
            }
        }
    }
    return (
        <div className='SettingBranchAdd'>
            <h1>지점 추가</h1>
            <p>
                지점명 :&nbsp;
                <input
                    type="text"
                    placeholder="지점명을 입력해주세요."
                    value={branchName}
                    onChange={(e:any) => setBranchName(e.target.value)}
                />
            </p>
            <p>
                지점코드 :&nbsp;
                <input
                    type="text"
                    placeholder="지점코드를 입력해주세요."
                    value={branchCode}
                    onChange={(e:any) => setBranchCode(e.target.value)}
                />
            </p>
            <button onClick={createBranch}>추가</button>

        </div>
    );
};

export default SettingBranchAdd;